.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 7rem;
  background-color: #2Ac1BC;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 5%;
  /*box-shadow: 0 2px 20px rgb(0, 0, 0);*/
  z-index: 10;
}

.header h1 {
	font-size: 5rem;
}

.header img {
	width: 14rem;
  height: 4rem;
  margin-left: -2rem;
}

.self {
  position: relative;
	padding-top: 15rem;
  margin-bottom: -3rem;
}

.main-image {
	mask-image: linear-gradient(to top, rgba(0,0,0,1), rgba(0,0,0,0));
	position: fixed;
	bottom: 0;
  width: 100%;
  z-index: -1;
  overflow: hidden;
}

.main-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
	/*transform: rotateZ(-5deg) translateY(-4rem) translateX(-1rem);*/
}