@font-face {
  font-family: "hanna";
  src: url("../../font/한나.otf");
}

@font-face {
  font-family: "air";
  src: url("../../font/한나air.otf");
}

.summary {
  text-align: center;
  max-width: 45rem;
  width: 90%;
  margin: auto;
  margin-top: 1rem;
  position: relative;
  background-color: #2Ac1BC;
  color: black;
  border-radius: 14px;
  padding: 1rem;
  z-index: 9;
  box-shadow: 0 2px 2px rgb(0, 0, 0);
}

.summary h1 {
  font-size: 2rem;
  margin-top: 0;
  font-family: "hanna";
}
.summary p {
  font-size: 1.5rem;
  margin-top: 0;
  font-family: "air";
}