.form {
  margin: 1rem 0;
  height: 17.5rem;
}

.control {
  margin-bottom: 0.5rem;
}

.mine {
  position: absolute;
  font-size: 1.2em;
  width: 3rem;
  height: 2rem;
  border:1px solid #ccc;
  background-color: rgba(204, 204, 204, 0);
  border-radius: 4px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.control2 label {
  font-weight: bold;
  font-size: 1.3rem;
  margin-bottom: 0.25rem;
  display: block;
}

.control2 input {
  font: inherit;
  font-size: 1.2em;
  border: 1px solid #ccc;
  border-right: 0;
  border-radius: 4px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0%;
  height: 2rem;
  width: 92%;
  max-width: 100%;
  margin-bottom: 0.25rem;
}


.control label {
  font-weight: bold;
  margin-bottom: 0.25rem;
  font-size: 1.3rem;
  display: block;
}

.control input {
  font: inherit;
  font-size: 1.2em;
  border: 1px solid #ccc;
  border-radius: 4px;
  height: 2rem;
  width: 100%;
  max-width: 100%;
}


.actions {
  display: flex;
  padding-top: 1rem;
  justify-content: flex-end;
  gap: 1rem;
}

.actions button {
  font: inherit;
  color: white;
  cursor: pointer;
  background-color: #2Ac1BC;
  border: none;
  border-radius: 25px;
  padding: 0.5rem 2rem; 
}

.actions button:hover,
.actions button:active {
  background-color: #2Ac1BC;
}

.actions .submit {
  border: 1px solid #2Ac1BC;
  background-color: #2Ac1BC;
  color: white;
}

.actions .submit:hover,
.actions .submit:active {
  background-color: #2Ac1BC;
}

/*.invalid label {
  color: #2Ac1BC;
}*/

.invalid input {
  border-color: red;
  background-color: #ffeff1;
}

.invalid button {
  border-color: red;
}