.input {
  display: flex;
  align-items: center;
  margin-bottom: 0.5rem;
}

.input label {
  font-weight: bold;
  margin-right: 0.7rem;
  margin-bottom: 1.1rem;
}

.input input {
  width: 3rem;
  border-radius: 5px;
  border: 1px solid #ccc;
  font: inherit;
  padding-left: 0.5rem;
  margin-bottom: 1rem;
}
.input input::-webkit-outer-spin-button,
.input input::-webkit-inner-spin-button {
  opacity: 1;
}